<template>
    <div>
        <v-pagination :value="page" :length="-~(total_number/10)" :dark="true" :total-visible="7" @input="info_"></v-pagination>
    </div>
</template>

<script>
export default {
    props:[
        "page",
        "total_number"
    ],
  components: {},
  data() {
    return {
        
    }
  },
  mounted() {
  },
  methods:{
      info_(e){
          this.$emit("opinionPage", e);
      }
  }
}
</script>